.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #fafafa;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 8vmin);
  color: #0a0a0a;
}

.App-link {
  color: #61dafb;
}

.pt {
  padding-top: 16px;
}

.projectBanner {
  padding: 12px;
  text-decoration: none!important;
}

.projectBanner h4 {
  font-size: calc(1px + 4vmin);
}

.projectBanner img {
  width: 80vmin;
}

.projectBanner p {
  font-size: 18px;
  text-decoration: none!important;
}
